<template>
  <div id="app">
    <HEADER
      v-if="!isShowHeader"
      :title="navTitle"
      :bgImage="bgImg"
      :bgColor="bgColor"
      @leftButton="handlerBack"
    ></HEADER>
    <transition name="bounce">
      <router-view :style="style" />
    </transition>
  </div>
</template>

<script>
import HEADER from "@/components/Header/index"
export default {
  name: "App",
  components: {
    HEADER,
  },
  data() {
    return {
      navTitle: "app",
      bgImg: "",
      bgColor: "",
      isShowHeader: false,
      navigatorHeight: this.headerHeight,
    }
  },
  computed: {
    style() {
      var style = `margin-top:${this.navigatorHeight}px;`
      return style
    },
  },
  methods: {
    // 
    handlerBack() {
      // 
      if (this.$route.meta.type) {
        this.$router.go(this.$route.meta.type)
      } else {
        // 
        this.$router.go(-1)
      }
    },
  },
  watch: {
    $route(to) {
      // 
      if (to.meta.title) {
        this.navTitle = to.meta.title
      } else {
        this.navTitle = ""
      }

      // 
      if (to.meta.customBgcImg) {
        this.bgImg = to.meta.customBgcImg
      } else {
        this.bgImg = ""
      }

      // 
      if (to.meta.bgColor) {
        this.bgColor = to.meta.bgColor
      } else {
        this.bgColor = ""
      }

      // header
      if (to.meta.isShowHeader == undefined) {
        this.isShowHeader = false
      } else {
        this.isShowHeader = to.meta.isShowHeader
      }
    },
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
</style>
